import client from '@/plugins/axios';

export const auth = {
  // LOGIN
  async login(payload) {
    return client.post('/login', payload);
  },

  // LOGOUT
  async logout() {
    return client.post('/logout');
  },

  // SIGNUP
  async signup(payload) {
    return client.post('/register', payload);
  },

  // CONFIRM EMAIL
  async confirmEmail(payload) {
    return client.post('/confirm_email', payload);
  },

  // SEND EMAIL CONFIRMATION
  async sendEmailConfirmation(payload) {
    return client.post('/send_email_confirmation', payload);
  },

  // FORGOT PASSWORD
  async forgotPassword(payload) {
    return client.post('/forgot_password', payload);
  },

  // RESET PASSWORD
  async resetPassword(payload) {
    return client.post('/reset_password', payload);
  },
};
