          </ErrorAlert>
          <p class="text">
            На ваш email было отправлено письмо с подтверждением. Кликните на
            кнопку в письме, чтобы использовать наш сервис.
          </p>
          <AppSpacer :size="25" vertical />
          <div class="form_group email">
            <input
              type="email"
              class="input"
              placeholder="Ваш email"
              :value="email"
              disabled
            />
          </div>
          <AppSpacer :size="35" vertical />
          <div class="noEmailRecieved">
