<script>
import { useVuelidate } from '@vuelidate/core';
import { required, email, minLength, helpers } from '@vuelidate/validators';
import AppSelect from '@/components/AppSelect.vue';
import AppButton from '@/components/AppButton.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';
import AppCheckbox from '@/components/AppCheckbox.vue';
import ErrorAlert from '@/components/ErrorAlert.vue';
import RecaptchaCompliance from '@/components/RecaptchaCompliance.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';

const { withMessage } = helpers;

export default {
  setup() {
    return {
      v$: useVuelidate(),
      appStore: useAppStore(),
      userStore: useUserStore(),
    };
  },
  data: () => ({
    modalType: null,
    step: 1,
    name: '',
    email: '',
    password: '',
    occupation: null,
    gift: true,
    listOccupations: [
      {
        label: 'Веб дизайн',
        value: 'web-design',
      },
      {
        label: 'Программирование',
        value: 'development',
      },
      {
        label: 'SMM менеджмент',
        value: 'smm-management',
      },
      {
        label: 'Алкоголизм',
        value: 'alcoholism',
      },
    ],
    isSigningIn: false,
  }),
  validations() {
    return {
      name: {
        required: withMessage(() => 'Имя не может быть пустым', required),
      },
      password: {
        required: withMessage(() => 'Пароль не может быть пустым', required),
        minLength: withMessage(({ $params }) => {
          return `Пароль должен быть не менее ${$params.min} знаков`;
        }, minLength(4)),
      },
      email: {
        required: withMessage(() => 'Email не может быть пустым', required),
        email: withMessage(() => 'Указан неверный формат email', email),
      },
      occupation: {
        required: withMessage(
          () => 'Вид деятельности не может быть пустым',
          required,
        ),
      },
    };
  },
  methods: {
    closeModal() {
      this.appStore.showModalAuth(false);
    },

    showSignIn() {
      this.appStore.showModalAuth('signin');
    },
    async signin() {
      this.v$.$touch();
      if (this.v$.email.$invalid || this.v$.password.$invalid) {
        return false;
      }

      try {
        this.isSigningIn = true;
        const data = await this.userStore.login({
          email: this.email,
          password: this.password,
        });

        if (data?.confirmed_at === null) {
          this.appStore.showModalAuth('emailConfirmation');
        } else if (data?.confirmed_at) {
          await this.userStore.getUser();
          // this.$router.push({ name: this.$route.params.nextRoute });
          this.$router.push({ name: 'adminBanners' });
          this.closeModal();
        }
        this.isSigningIn = false;
      } catch (e) {
        console.error('Не получилось авторизоваться');
      }
    },

    showSignUp() {
      this.appStore.showModalAuth('signup');
    },
    signupNextStep() {
      this.v$.$touch();
      if (this.v$.email.$invalid || this.v$.password.$invalid) {
        return false;
      }
      this.v$.$reset?.();

      this.step++;
    },
    async signup() {
      this.v$.$touch();
      if (
        this.v$.email.$invalid ||
        this.v$.password.$invalid ||
        this.v$.name.$invalid ||
        this.v$.occupation.$invalid
      ) {
        return false;
      }

      try {
        await this.userStore.signup({
          name: this.name,
          email: this.email,
          password: this.password,
          occupation: this.occupation,
          gift: this.gift,
        });

        this.appStore.showModalAuth('emailConfirmation');
      } catch (e) {
        console.error('Не получилось зарегистрироваться');
      }
    },

    showForgotPassword() {
      this.appStore.showModalAuth('forgotPassword');
    },
    async forgotPasswordNextStep() {
      this.v$.$touch();
      if (this.v$.email.$invalid) {
        return false;
      }

      try {
        await this.userStore.forgotPassword({
          email: this.email,
        });
        this.step++;
      } catch (error) {
        console.error('Не получилось отправить письмо с подверждением');
      }
    },

    showEmailConfirmation() {
      this.appStore.showModalAuth('emailConfirmation');
    },
    async resendEmailConfirmation() {
      try {
        await this.userStore.sendEmailConfirmation({
          email: this.email,
        });
        this.step++;
      } catch (error) {
        console.error('Не получилось отправить письмо с подверждением');
      }
    },
    async confirmEmail() {
      try {
        this.appStore.showModalAuth('emailVerify');
        await this.userStore.confirmEmail({
          verify: this.$route.query.verify,
        });
      } catch (error) {
        console.error('Не получилось подтвердить email');
      }
    },

    showResetPassword() {
      this.appStore.showModalAuth('resetPassword');
    },
    async resetPassword() {
      this.v$.$touch();
      if (this.v$.password.$invalid) {
        return false;
      }

      try {
        await this.userStore.resetPassword({
          password: this.password,
          token: this.$route.query.reset,
        });
        this.step++;
      } catch (error) {
        console.error('Не получилось обновить пароль');
      }
    },
  },
  computed: {
    modalAuthType() {
      return this.appStore.isModalAuthVisible;
    },
    authStatus() {
      return this.userStore.authStatus;
    },
    signupStatus() {
      return this.userStore.signupStatus;
    },
    sendEmailConfirmationStatus() {
      return this.userStore.sendEmailConfirmationStatus;
    },
    confirmEmailStatus() {
      return this.userStore.confirmEmailStatus;
    },
    forgotPasswordStatus() {
      return this.userStore.forgotPasswordStatus;
    },
    resetPasswordStatus() {
      return this.userStore.resetPasswordStatus;
    },
    isModalVisible() {
      return !!this.modalAuthType;
    },
    isLoaderVisible() {
      return this.confirmEmailStatus.type === 'loading';
    },
  },
  watch: {
    modalAuthType: {
      immediate: true,
      handler(newVal, oldVal) {
        this.step = 1;

        if (this.modalAuthType) {
          this.modalType = this.modalAuthType;
        }

        if (oldVal === 'emailVerify') {
          const query = { ...this.$route.query };
          delete query.verify;
          this.$router.replace({ query });
        }
        if (oldVal === 'resetPassword') {
          const query = { ...this.$route.query };
          delete query.reset;
          this.$router.replace({ query });
        }

        if (this.modalAuthType === 'emailConfirmation') {
          return;
        }

        setTimeout(() => {
          this.name = '';
          this.email = '';
          this.password = '';
          this.occupation = null;
          this.v$.$reset?.();
        }, 300);
      },
    },
    $route: {
      immediate: true,
      deep: true,
      handler() {
        if (this.$route.query.verify) {
          this.confirmEmail();
        }
        if (this.$route.query.reset) {
          this.showResetPassword();
        }
        if (this.$route.params.needsAuth) {
          this.showSignIn();
        }
      },
    },
  },
  components: {
    AppInput,
    AppButton,
    AppSelect,
    AppCheckbox,
    RecaptchaCompliance,
    ErrorAlert,
  },
};
</script>

