      <button class="modal_close" @click="closeModal"><span></span></button>
      <h3>
        You liked the service - please tell us about it on social networks
      </h3>
      <div class="social">
        <a
          v-for="{ badge, href, id } of social"
          :key="id"
          :href="href"
          target="_blank"
          no-referrer
        >
          <component :is="badge" />
        </a>
      </div>
      <div class="description">
        Our service is completely <span>free</span> and we are pleased if many
        people use it. We do not charge for this fee, we only ask you to tell us
        as much as possible about it, this gives us additional motivation to
        move on.
      </div>
      <div class="support">Support us in product hunt</div>
      <a class="productHunt" href="#" target="_blank" no-referrer>
        <ProductHuntIcon />
      </a>
    </div>
  </AppModal>
