<script>
import TwitterBadge from './badges/TwitterBadge.vue';
import LinkedinBadge from './badges/LinkedinBadge.vue';
import FacebookBadge from './badges/FacebookBadge.vue';
import ProductHuntIcon from './icons/ProductHuntIcon.vue';
import { useAppStore } from '@/stores/useAppStore';

const social = [
  {
    id: 'twitter',
    href: '#',
    badge: TwitterBadge,
  },
  {
    id: 'linkedin',
    href: '#',
    badge: LinkedinBadge,
  },
  {
    id: 'facebook',
    href: '#',
    badge: FacebookBadge,
  },
];

export default {
  setup() {
    return {
      appStore: useAppStore(),
      social,
    };
  },
  computed: {
    isModalVisible() {
      return this.appStore.isModalFeedbackVisible;
    },
  },
  methods: {
    closeModal() {
      this.appStore.showModalFeedback(false);
    },
  },
  components: {
    ProductHuntIcon,
  },
};
</script>

