<template>
  <div class="card">
    <div class="title" v-if="!imagePreview">{{ label }}</div>
    <div
      class="imagePreview"
      v-if="imagePreview"
      :style="`background-image: url('${imagePreview}');`"
    />
    <component v-else :is="icon" class="imageIcon" />
    <label color="#A9A9AA" class="inputLabel">
      <span class="buttonText">{{ placeholder }}</span>
      <AppInput
        v-model="image"
        type="file"
        @change="fileUpdate"
        class="input"
      />
    </label>
    <div class="fileName">{{ imageName }}</div>
  </div>
</template>

<script>
import ImageIcon from '../../../../components/icons/ImageIcon.vue';
import AppButton from '@/components/AppButton.vue';
import AppInput from '@/components/inputs/AppInput/AppInput.vue';

export default {
  props: {
    label: {
      type: String,
      required: true,
    },
    placeholderOnUploaded: {
      type: String,
      required: true,
    },
    placeholderForUpload: {
      type: String,
      required: true,
    },
    icon: {
      type: Object,
      required: true,
    },
  },
  data: () => ({
    image: null,
    imageName: null,
    imagePreview: null,
  }),
  methods: {
    validateImage(value) {
      return this.imagePreview || value;
    },
    fileUpdate() {
      if (this.image?.file) {
        const reader = new FileReader();
        reader.readAsDataURL(this.image.file);
        reader.onload = (e) => {
          this.imagePreview = e.target.result || this.imagePreview;

          this.imageName = this.image.name;
        };
      }
    },
  },
  computed: {
    placeholder() {
      return this.imagePreview
        ? this.placeholderOnUploaded
        : this.placeholderForUpload;
    },
  },
  components: { ImageIcon, AppButton, AppInput },
};
</script>

