import mock from '@/mock/mock';

// GET CATEGORIES
mock.onGet('/categories').reply(200, {
  categories: [
    {
      id: 23535,
      name: 'Fancy-Shmancy',
      image: require('@/assets/images/emoji-gift-bags.png'),
      color: '#FFCB45',
    },
  ],
});

// CREATE CATEGORY
mock.onPost('/categories').timeoutOnce();

// UPDATE CATEGORY
mock.onPatch(/\/categories\/\d+$/).reply(200);

// DELETE CATEGORY
mock.onDelete(/\/categories\/\d+$/).timeoutOnce();
mock.onDelete(/\/categories\/\d+$/).reply(200);

// RESTORE CATEGORY
mock.onPost(/\/categories\/\d+\/restore$/).timeoutOnce();
mock.onPost(/\/categories\/\d+\/restore$/).reply(200);
