<template>
  <div class="bannerCard">
    <ResizableContainer class="bannerCard--canvas">
      <template #default="{ containerWidth }">
        <BannerCanvas
          :canvas="canvas"
          :scheme="shemeId"
          :banner="banner.data"
          :zoom="containerWidth / canvas.width"
          rounded
        />
      </template>
    </ResizableContainer>

    <div class="card-metrics" v-if="showConversion || showDownloadsCount">
      <StatisticBlock
        v-if="showConversion"
        desciption="Conversion"
        :value="conversion"
        valuePostscript="%"
        tooltipValue="blahblah"
      />
      <StatisticBlock
        v-if="showDownloadsCount"
        desciption="Number of downloads"
        :value="downloadsCount"
        valuePostscript="times"
        tooltipValue="blahblah"
      />
    </div>

    <div class="availableScreenSizes" v-if="!isSimilar">
      <BadgeWithCheckbox
        v-for="{ value, label } in sizes"
        :key="value"
        :value="true"
        :isPermanentValue="true"
      >
        {{ value }} {{ label }}
      </BadgeWithCheckbox>
    </div>
    <div v-if="isSimilar" class="similarBottomButtons">
      <router-link
        :to="{ name: 'userEditBanner', params: { id: banner.id } }"
        class="banner_actions_button banner_actions_button--edit"
      >
        Редактировать
        <AppIcon icon="arrows-right" />
      </router-link>
    </div>
    <div class="buttons" v-if="!isSimilar">
      <AppButton color="primary" @click="onDownload">Free download</AppButton>
      <AppButton color="black" @click="onEditBanner">Edit Banner</AppButton>
    </div>
  </div>
</template>

<script>
import AppButton from '@/components/AppButton.vue';
import StatisticBlock from '@/components/StatisticBlock/StatisticBlock.vue';
import BadgeWithCheckbox from '@/components/BadgeWithCheckbox/BadgeWithCheckbox.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';
import BannerCanvas from '../BannerCanvas.vue';
import ResizableContainer from '../ResizableContainer/ResizableContainer.vue';

export default {
  props: {
    banner: {
      type: Object,
    },
    isSimilar: {
      type: Boolean,
    },
  },
  setup() {
    return {
      appStore: useAppStore(),
      userStore: useUserStore(),
    };
  },
  computed: {
    shemeId() {
      return this.banner.data.schemes[0]?.id;
    },
    sizes() {
      return this.banner.data.canvases.map(({ height, width, title }) => ({
        value: `${width}x${height}`,
        label: title,
      }));
    },
    canvas() {
      return this.banner.data.canvases[0];
    },
    conversion() {
      return this.banner.data.conversion;
    },
    downloadsCount() {
      return this.banner.data.used;
    },
    showConversion() {
      return this.banner.data.conversion || this.banner.data.conversion === 0;
    },
    showDownloadsCount() {
      return this.banner.data.used || this.banner.data.used === 0;
    },
    isAuth() {
      return this.userStore.isAuthenticated;
    },
  },
  methods: {
    async onDownload() {
      // Download stuff...

      this.appStore.showModalFeedback(true);
    },
    async onEditBanner() {
      if (this.isAuth) {
        this.$router.push({
          name: 'userEditBanner',
          params: { id: this.banner.id },
        });
      }
    },
  },
  components: {
    AppButton,
    BadgeWithCheckbox,
    BannerCanvas,
    StatisticBlock,
    ResizableContainer,
  },
};
</script>

