<template>
  <div :class="className" ref="resizableContainer">
    <slot :containerWidth="containerWidth" />
  </div>
</template>

<script>
export default {
  props: {
    className: { type: String },
  },
  data() {
    return {
      containerWidth: 0,
      resizeObserver: null,
    };
  },
  mounted() {
    this.init();
    this.calculateCanvasWidth();
  },
  methods: {
    init() {
      this.resizeObserver = new ResizeObserver((entries) => {
        for (let entry of entries) {
          this.containerWidth = entry.contentRect.width;
        }
      });

      this.resizeObserver.observe(this.$refs.resizableContainer);
    },
    calculateCanvasWidth() {
      this.containerWidth = this.$refs.resizableContainer.offsetWidth;
    },
  },
  beforeUnmount() {
    this.resizeObserver.disconnect();
  },
};
</script>

<style lang="scss" scoped></style>
