<template>
  <div class="container">
    <WithLabel :max="max" :label="label" :value="modelValue">
      <AppInput
        :id="id"
        v-model="localModelValue"
        :placeholder="placeholder"
        type="text"
        :invalid="invalid"
        :error="error"
      />
    </WithLabel>
  </div>
</template>

<script>
import { generateId } from '@/utils/generateId';
import AppInput from '../AppInput/AppInput.vue';
import WithLabel from '../../WithLabel/WithLabel.vue';

export default {
  setup() {
    return {
      id: generateId(),
    };
  },
  props: {
    modelValue: { type: null },
    label: { type: String },
    placeholder: { type: String },
    max: { type: String },
    invalid: { type: null },
    error: { type: String },
  },
  computed: {
    localModelValue: {
      get() {
        return this.modelValue;
      },
      set(newValue) {
        this.$emit('update:modelValue', newValue);
      },
    },
  },
  components: {
    AppInput,
    WithLabel,
  },
};
</script>

