<template>
  <div>
    <h2 class="title">
      Ai generate banner
      <IconWithTooltip />
    </h2>
    <form @submit.prevent="submit" novalidate class="wrapper">
      <div class="top">
        <div class="form">
          <InputWithLabel
            v-model="formData.offer"
            label="Your offer"
            placeholder="Help your business"
            type="text"
            max="30"
            :invalid="v$.formData.offer.$invalid && v$.formData.offer.$dirty"
            :error="v$.formData.offer.$errors[0]?.$message"
          />
          <InputWithLabel
            v-model="formData.description"
            label="Description"
            placeholder="Start your ads from us"
            type="text"
            max="54"
          />
          <WithLabel label="Category company">
            <vSelect
              :options="companyCategories"
              v-model="formData.companyCategory"
              :clearable="false"
              placeholder="Crypto Assets"
            />
          </WithLabel>
          <WithLabel label="Brand color">
            <ColorsList :colorList="colorList" v-model="formData.color" />
          </WithLabel>
        </div>
        <div class="uploaders">
          <UploadCard
            v-for="{
              label,
              icon,
              placeholderOnUploaded,
              placeholderForUpload,
            } in uploaders"
            :key="label"
            :label="label"
            :icon="icon"
            :placeholderOnUploaded="placeholderOnUploaded"
            :placeholderForUpload="placeholderForUpload"
          />
        </div>
      </div>
      <div class="bottom">
        <AppButton
          :disabled="v$.formData.offer.$errors[0]"
          color="primary"
          @click="getGeneratedBanners"
        >
          <img
            src="../../../assets/images/smallCapture.png"
            width="20"
            class="icon"
          />
          {{ generateBannersLabel }}
        </AppButton>
        <div class="sizes">
          <span class="availableSizes">Available sizes</span>
          <div class="sizesContainer">
            <div
              v-for="[label] in availableSizes"
              v-bind:key="label"
              class="sizesLabel"
            >
              {{ label }}
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import AppButton from '@/components/AppButton.vue';
import InputWithLabel from '@/components/inputs/InputWithLabel/InputWithLabel.vue';
import vSelect from 'vue-select';
import WithLabel from '@/components/WithLabel/WithLabel.vue';
import ColorsList from './ColorsList/ColorsList.vue';
import { availableSizes, colorList } from './constants';
import UploadCard from './UploadCard/UploadCard.vue';
import ImageIcon from '@/components/icons/ImageIcon.vue';
import ExampleLogoIcon from '@/components/icons/ExampleLogoIcon.vue';
import IconWithTooltip from '@/components/IconWithTooltip/IconWithTooltip.vue';
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';

const uploaders = [
  {
    label: 'Image product / website / app',
    icon: ImageIcon,
    placeholderOnUploaded: 'Load image',
    placeholderForUpload: 'Change image',
  },
  {
    label: 'Logotype Company',
    icon: ExampleLogoIcon,
    placeholderOnUploaded: 'Load logo',
    placeholderForUpload: 'Change logo',
  },
];

export default {
  setup() {
    return {
      v$: useVuelidate(),
      availableSizes: Object.entries(availableSizes),
      colorList,
      uploaders,
    };
  },
  props: {
    onSubmit: { type: Function },
  },
  data() {
    return {
      loading: false,
      formData: {
        description: '',
        offer: '',
        companyCategory: null,
        color: null,
      },
      companyCategories: ['1', 2, 3],
    };
  },
  validations() {
    return {
      formData: {
        offer: {
          required,
        },
      },
    };
  },
  methods: {
    async getGeneratedBanners() {
      const isDataCorrect = await this.v$.$validate();

      if (!isDataCorrect) {
        return;
      }

      this.loading = true;

      await this.onSubmit(this.formData);

      this.loading = false;
    },
  },
  computed: {
    generateBannersLabel() {
      return this.loading ? 'Loading...' : 'Ai Generate banner';
    },
  },
  components: {
    AppButton,
    InputWithLabel,
    vSelect,
    WithLabel,
    ColorsList,
    UploadCard,
    IconWithTooltip,
  },
};
</script>

<style lang="scss" scoped>
.title {
  margin-bottom: 20px;
}

.wrapper {
  height: 370px;
  background: #ffffff;
  border-radius: 25px;

  .top {
    display: flex;
    height: calc(100% - 100px);
    padding: 36px 57px 44px 57px;
    .form {
      display: grid;
      column-gap: 15px;
      row-gap: 30px;
      grid-template-rows: 80px 80px;
      grid-template-columns: 250px 343px;
    }
    .uploaders {
      margin-left: 80px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  .bottom {
    padding: 0 57px;
    display: flex;
    align-items: center;
    height: 100px;
    background: #f8f8f8;
    border-radius: 0px 0px 25px 25px;

    .sizes {
      margin-left: 54px;
      display: flex;
      align-items: center;

      .availableSizes {
        margin-right: 25px;
        font-weight: 600;
        font-size: 14px;
        line-height: 171%;
        color: #989898;
      }

      .sizesContainer {
        display: flex;
        align-items: center;

        & > *:not(:last-child) {
          margin-right: 20px;
        }

        .sizesLabel {
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          line-height: 171%;
          color: #25262c;
        }
      }
    }
  }
}
</style>
