<template>
  <div class="container">
    <div
      v-for="color in colorList"
      :key="color"
      @click="() => onChangeColor(color)"
      class="colorCheckbox"
      :style="{ backgroundColor: color }"
    >
      <CheckMarkIcon v-if="color === modelValue" />
    </div>
  </div>
</template>

<script>
import CheckMarkIcon from '@/components/icons/CheckMarkIcon.vue';

export default {
  props: {
    colorList: {
      required: true,
      type: Array,
    },
    modelValue: {
      type: null,
    },
  },
  methods: {
    onChangeColor(color) {
      this.$emit('update:modelValue', color);
    },
  },
  components: { CheckMarkIcon },
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  .colorCheckbox {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 5px;
    }

    &:first-child {
      border: 2px solid #f2f2f2;
    }
  }
}
</style>
