<template>
  <div class="userDropdown" :class="{ 'is-active': isDropdownVisible }">
    <AppButton
      class="userDropdown_trigger"
      color="grey"
      @click.stop="toggleDropdown"
    >
      <img
        v-if="user.hasPremium"
        src="../assets/images/emoji-crown.png"
        width="20"
        class="icon"
      />
      {{ user.name }}
      <AppIcon
        icon="chevron-down"
        size="0.75em"
        right
        class="userDropdown_trigger_arrow"
      />
    </AppButton>
    <ul class="userDropdown_list">
      <li>
        <AppButton color="black" blank>
          <img src="../assets/images/emoji-crown.png" width="20" class="icon" />
          <span>{{ $t('app.subscription') }}</span>
        </AppButton>
      </li>
      <li>
        <AppButton color="black" blank>
          <img
            src="../assets/images/emoji-settings.png"
            width="20"
            class="icon"
          />
          <span>{{ $t('app.settings') }}</span>
        </AppButton>
      </li>
      <li>
        <AppButton color="black" blank @click="logout">
          <AppIcon icon="logout" :size="20" />
          <span>{{ $t('app.logout') }}</span>
        </AppButton>
      </li>
    </ul>
  </div>
</template>

<script>
import AppButton from '@/components/AppButton.vue';
import { useUserStore } from '@/stores/useUserStore';

export default {
  name: 'UserDropdown',
  setup() {
    return {
      userStore: useUserStore(),
    };
  },
  data: () => ({
    isDropdownVisible: false,
  }),
  components: {
    AppButton,
  },
  computed: {
    user() {
      return this.userStore.user;
    },
  },
  methods: {
    toggleDropdown() {
      this.isDropdownVisible = !this.isDropdownVisible;

      document.addEventListener(
        'click',
        () => {
          this.isDropdownVisible = false;
        },
        { once: true },
      );
    },
    logout() {
      this.userStore.logout();
    },
  },
};
</script>

<style lang="scss" scoped>
.userDropdown {
  position: relative;
  z-index: 10;
}
.userDropdown_trigger_arrow {
  transition: 0.175s ease;

  .userDropdown.is-active & {
    transform: scaleY(-1);
  }
}
.userDropdown_list {
  right: 0;
  top: 100%;
  margin-top: 5px;
  border-radius: 15px;
  width: 200px;
  padding: 0.5em 0;
  overflow: hidden;
  background: var(--color-grey);
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: 0.125s ease;
  transform: translateY(15px);
  font-weight: 600;
  line-height: 113.6%;

  .userDropdown.is-active & {
    opacity: 1;
    transform: none;
    visibility: visible;
  }
  .button {
    padding: 0.5em 1em;
  }
}
</style>
