<template>
  <div class="main_column">
    <header class="header">
      <router-link :to="{ name: 'home' }" class="logo">
        <AppIcon icon="logo" :size="30" />
      </router-link>
      <div class="countdown">
        <span>
          {{ isAuth ? $t('app.nextBannersIn') : $t('app.bannerInOneMinute') }}
        </span>
        <div class="countdown_timer" v-if="isAuth">
          <span v-if="countdown.days">
            {{ countdown.days }} {{ $tc('app.days', countdown.days) }}
          </span>
          <span v-if="countdown.hours">
            {{ countdown.hours }} {{ $tc('app.hours', countdown.hours) }}
          </span>
          <span v-if="countdown.minutes">
            {{ countdown.minutes }}
            {{ $tc('app.minutes', countdown.minutes) }}
          </span>
          <span v-if="countdown.seconds >= 0 && !countdown.days">
            {{ countdown.seconds }}
            {{ $tc('app.seconds', countdown.seconds) }}
          </span>
        </div>
      </div>

      <ul class="header_nav">
        <!-- Admin buttons -->
        <li v-if="isAuth && isAdmin" class="header_nav_item">
          <AppButton color="black" href="https://tilda.cc" target="_blank">
            <img src="../assets/images/tilda.png" width="24" class="icon" />
            {{ $t('app.gotToTilda') }}
          </AppButton>
        </li>
        <li v-if="isAuth && isAdmin" class="header_nav_item">
          <AppButton color="golden" :to="{ name: 'usersList' }">
            <img src="../assets/images/emoji-man.png" width="20" class="icon" />
            {{ $t('app.allUsers') }}
          </AppButton>
        </li>

        <!-- User buttons -->
        <li v-if="!isAdmin" class="header_nav_item">
          <AppButton color="white">
            <img
              src="../assets/images/emoji-videocamera.png"
              width="20"
              class="icon"
            />
            {{ $t('app.howItWorks') }}
          </AppButton>
        </li>
        <li v-if="!isAdmin && !hasPremium" class="header_nav_item">
          <AppButton color="golden">
            <img
              src="../assets/images/emoji-crown.png"
              width="20"
              class="icon"
            />
            {{ $t('app.getPremium') }}
          </AppButton>
        </li>
        <li v-if="!isAdmin" class="header_nav_item">
          <AppButton color="primary" :to="{ name: 'home' }">
            <img
              src="../assets/images/emoji-picture.png"
              width="18"
              class="icon"
            />
            {{ $t('app.create') }}
          </AppButton>
        </li>

        <!-- Auth buttons -->
        <li v-if="isAuth" class="header_nav_item">
          <UserDropdown />
        </li>
        <li v-if="!isAuth" class="header_nav_item">
          <AppButton outlined color="black" @click="showModalSignin">
            {{ $t('app.login') }}
          </AppButton>
        </li>
      </ul>
    </header>
  </div>
</template>

<script>
import AppButton from '@/components/AppButton.vue';
import UserDropdown from '@/components/UserDropdown.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';

export default {
  name: 'AppHeader',
  setup() {
    return {
      appStore: useAppStore(),
      userStore: useUserStore(),
    };
  },
  components: {
    AppButton,
    UserDropdown,
  },
  methods: {
    showModalSignin() {
      this.appStore.showModalAuth('signin');
    },
  },
  computed: {
    isAuth() {
      return this.userStore.isAuthenticated;
    },
    isAdmin() {
      return this.userStore.isAdmin;
    },
    hasPremium() {
      return this.userStore.hasPremium;
    },
    countdown() {
      return this.appStore.countdown;
    },
  },
};
</script>

<style lang="scss" scoped>
.header {
  display: flex;
  align-items: center;
  height: 80px;
}
.logo {
  color: inherit;
}

.countdown {
  margin-right: auto;
  margin-left: 35px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1em;
  align-items: center;
  color: #989898;
}
.countdown_timer {
  color: var(--color-accent);
  display: inline;

  span {
    &::before {
      content: ' ';
    }
  }
}

.header_nav {
  display: flex;

  &_item + &_item {
    margin-left: 5px;
  }
}
</style>
