<template>
  <AppLoader />
  <div>
    Временный роутинг
    <br />
    <router-link to="/"> Main </router-link>
    <br />
    <router-link to="/login"> Login </router-link>
    <br />
    <router-link to="/admin"> Admin banners </router-link>
    <br />
    <router-link to="/user/edit/46"> User edit banner </router-link>
    <br />
    <router-link to="/admin/banner/46"> Admin edit banner </router-link>
  </div>

  <AppHeader v-if="isAuth" />
  <router-view />
  <AppFooter v-if="isAuth" />
  <ModalAuth />
  <FeedbackModal />
  <SvgIconsSprite />
</template>

<script>
import dayjs from '@/plugins/dayjs';
import getScrollbarWidth from '@/utils/getScrollbarWidth';
import AppLoader from '@/components/AppLoader.vue';
import AppHeader from '@/components/AppHeader.vue';
import AppFooter from '@/components/AppFooter.vue';
import ModalAuth from '@/components/ModalAuth.vue';
import SvgIconsSprite from '@/components/SvgIconsSprite';
import { useAppStore } from '@/stores/useAppStore';
import { useUserStore } from '@/stores/useUserStore';
import { useColorsStore } from '@/stores/useColorsStore';
import { useCategoriesStore } from '@/stores/useCategoriesStore';
import 'vue-toastification/dist/index.css';
import '@/assets/styles/fonts/fonts.scss';
import '@/assets/styles/animations.css';
import '@/assets/styles/modal.css';
import '@/assets/styles/vue-select.css';
import '@/assets/styles/vue-color-kit.css';
import FeedbackModal from './components/modals/FeedbackModal/FeedbackModal.vue';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      userStore: useUserStore(),
      colorsStore: useColorsStore(),
      categoriesStore: useCategoriesStore(),
    };
  },
  data: () => ({
    scrollbarWidth: getScrollbarWidth(),
    dayjs,
  }),
  components: {
    AppLoader,
    AppFooter,
    AppHeader,
    ModalAuth,
    SvgIconsSprite,
    FeedbackModal,
  },
  computed: {
    isAnyModalVisible() {
      return this.appStore.isAnyModalVisible;
    },
    isAuth() {
      return this.userStore.isAuthenticated;
    },
    countdown() {
      return this.appStore.countdown;
    },
  },
  methods: {
    init() {
      const ro = new ResizeObserver(() => {
        this.scrollbarWidth = getScrollbarWidth();
      });
      ro.observe(document.body);

      this.categoriesStore.getCategories();
      this.colorsStore.getColors();

      if (this.isAuth) {
        this.userStore.getUser();
      }

      this.setCountdown();
    },
    setCountdown() {
      const today = this.dayjs().tz();
      const nextMonday = this.dayjs()
        .tz()
        .add(today.day() > 0 ? 1 : 0, 'week')
        .day(1)
        .hour(0)
        .minute(0)
        .second(0);
      let diff = nextMonday.diff(today, 'seconds');

      this.appStore.setCountdownTime(diff);

      setInterval(() => {
        this.appStore.setCountdownTime(this.countdown.time - 1);
      }, 1000);
    },
  },
  watch: {
    isAnyModalVisible: {
      immediate: true,
      handler() {
        const hasPageScrollbar =
          document.body.scrollHeight > window.innerHeight;
        if (this.isAnyModalVisible && hasPageScrollbar) {
          window.document.body.classList.add('hide-scrollbar');
        } else {
          setTimeout(() => {
            window.document.body.classList.remove('hide-scrollbar');
          }, 300);
        }
      },
    },
    scrollbarWidth: {
      immediate: true,
      handler() {
        document.documentElement.style.setProperty(
          '--scrollbar-width',
          `${this.scrollbarWidth}px`,
        );
      },
    },
  },
  mounted() {
    this.init();
  },
};
</script>

