<template>
  <label class="checkbox">
    <input type="checkbox" :name="name" @change="update" :checked="isChecked" />
    <div class="checkbox_box"><span></span></div>
    <div class="checkbox_label" v-if="!hideValue">
      {{ modelValue }}
      <slot />
    </div>
  </label>
</template>

<script>
import { isArray } from '@/utils/isArray';
export default {
  setup() {
    return {
      isArray,
    };
  },
  props: {
    name: {
      type: String,
    },
    value: {
      type: [String, Number, Boolean],
      default: true,
    },
    modelValue: {
      type: null,
    },
    isPermanentValue: {
      type: Boolean,
    },
    hideValue: {
      type: Boolean,
    },
  },
  methods: {
    update($event) {
      if (this.isPermanentValue) {
        $event.target.checked = this.value;

        return;
      }

      if ($event.target.checked) {
        this.check();
      } else {
        this.uncheck();
      }
    },
    check() {
      if (this.isArray(this.modelValue)) {
        const listValues = this.modelValue.slice(0); // Clone array

        listValues.push(this.value);
        this.$emit('update:modelValue', listValues);

        return;
      }

      this.$emit('update:modelValue', this.value);
    },
    uncheck() {
      if (this.isArray(this.modelValue)) {
        const listValues = this.modelValue.slice(0); // Clone array
        const valueIndex = listValues.indexOf(this.value);

        listValues.splice(valueIndex, 1); // Remove current value from array

        this.$emit('update:modelValue', listValues);

        return;
      }

      if (this.value === true) {
        this.$emit('update:modelValue', false);

        return;
      }

      this.$emit('update:modelValue', null);
    },
  },
  computed: {
    isChecked() {
      if (this.isPermanentValue) {
        return this.value;
      }

      return (
        (!isArray(this.modelValue) && this.modelValue) ||
        this.modelValue?.includes?.(this.value)
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.checkbox {
  align-items: center;
  display: flex;
  position: relative;
  cursor: pointer;

  input {
    opacity: 0;
    position: absolute;
    width: 1px;
    height: 1px;
    z-index: -1;

    &:checked ~ .checkbox_box {
      box-shadow: 0 0 0 10px var(--color-accent) inset;
      animation: shrink-bounce 150ms cubic-bezier(0.4, 0, 0.23, 1);

      span {
        color: #fff;

        &::before {
          transform: scaleY(1);
          transition: 35ms 150ms ease-in;
        }
        &::after {
          transform: scaleX(1);
          transition: 85ms 185ms ease-out;
        }
      }
    }
  }
  &_box {
    width: 20px;
    height: 20px;
    box-shadow: 0 0 0 2px #cfcfcf inset;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    transition: all 250ms cubic-bezier(0.4, 0, 0.23, 1);
    position: relative;
    margin-right: 10px;
    will-change: box-shadow;

    span {
      width: 15px;
      height: 15px;
      position: absolute;
      transform: translate(-1px, -6px) rotate(-45deg);
      will-change: transform;
      color: transparent;

      &::before,
      &::after {
        content: '';
        background: currentColor;
        position: absolute;
        bottom: 0;
        left: 0;
      }
      &::before {
        height: 6px;
        width: 2px;
        transform-origin: 0 0;
        transform: scaleY(0);
      }
      &::after {
        height: 2px;
        width: 12px;
        transform-origin: left center;
        transform: scaleX(0);
      }
    }
  }
}

@keyframes shrink-bounce {
  0% {
    transform: scale(1);
  }
  33% {
    transform: scale(0.85);
  }
  100% {
    transform: scale(1);
  }
}
</style>
