<template>
  <div class="title" id="bannersList">All our banner</div>
  <div class="noBanners" v-if="bannersIsEmpty">
    Банеров по Вашему запросу нет :(
  </div>
  <div class="container" v-else>
    <BannerCard v-for="banner of banners" :key="banner.id" :banner="banner" />
  </div>
  <!-- <div class="loadMore" v-if="!bannersIsEmpty" @click="onLoadMore">
    <span>load more</span><LoadMore />
  </div> -->
</template>

<script>
// import LoadMore from '../../../components/icons/LoadMore.vue';
import BannerCard from '@/components/BannerCard/BannerCard.vue';

export default {
  props: {
    onLoadMore: {
      type: Function,
    },
    banners: {
      required: true,
      type: [],
    },
  },
  computed: {
    bannersIsEmpty() {
      if (!this.banners) {
        return false;
      }

      return this.banners.length === 0;
    },
  },
  components: { BannerCard },
};
</script>

