import { createRouter, createWebHistory } from 'vue-router';
import { useUserStore } from '@/stores/useUserStore';
import BannerGenerator from '../views/BannerGenerator/BannerGenerator.vue';

const routes = [
  {
    path: '/login',
    name: 'HomeView',
    component: () =>
      import(/* webpackChunkName: "userEditor" */ '../views/HomeView.vue'),
  },
  {
    path: '/',
    name: 'home',
    component: BannerGenerator,
  },
  {
    path: '/user/edit/:id',
    name: 'userEditBanner',
    component: () =>
      import(
        /* webpackChunkName: "userEditor" */ '../views/UserEditor/UserEditor.vue'
      ),
  },
  {
    path: '/banner/:id',
    name: 'banner',
    component: () =>
      import(/* webpackChunkName: "banner" */ '../views/BannerView.vue'),
    props: true,
  },
  {
    path: '/admin/',
    name: 'adminBanners',
    component: () =>
      import(/* webpackChunkName: "admin" */ '../views/admin/BannersList.vue'),
    meta: {
      authOnly: true,
    },
  },
  {
    path: '/admin/banner/new',
    name: 'addBanner',
    component: () =>
      import(/* webpackChunkName: "admin" */ '../views/admin/BannerEditor.vue'),
    meta: {
      authOnly: true,
    },
  },
  {
    path: '/admin/banner/:id',
    name: 'editBanner',
    component: () =>
      import(/* webpackChunkName: "admin" */ '../views/admin/BannerEditor.vue'),
    props: true,
    meta: {
      authOnly: true,
    },
  },
  {
    path: '/admin/users',
    name: 'usersList',
    component: () =>
      import(/* webpackChunkName: "admin" */ '../views/admin/UsersList.vue'),
    props: true,
    meta: {
      authOnly: true,
    },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else if (to.hash) {
      return { selector: to.hash };
    } else {
      return {
        x: 0,
        y: 0,
      };
    }
  },
});

router.beforeEach((to, from) => {
  const userStore = useUserStore();
  if (
    to.matched.some((record) => record.meta.authOnly) &&
    !userStore.isAuthenticated
  ) {
    return {
      name: from?.name || 'home',
      params: {
        nextRoute: to.name,
        needsAuth: true,
      },
    };
  }
});

export default router;
